@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fade-in {
  animation: fadein 1s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

* {
  font-family: "Rubik";
  margin: 0;
}
:root {
  --border-circle-size: 260px;
  --background-color-dark: #181830;
  --background-color-light: #463c64;
  --font-color: #d1d1d1;
  --pink: #e53d8b;
  --light-blue: #249ade;
}

.App {
  height: 100vh;
  background: linear-gradient(
    to bottom,
    #463c64,
    #181830,
    #181830,
    #181830);
  background: linear-gradient(
    to bottom,
    var(--background-color-light),
    var(--background-color-dark),
    var(--background-color-dark),
    var(--background-color-dark)
  );
  display: flex;
  justify-content: center;
  align-items: center;
  color: #d1d1d1;
  color: var(--font-color);
}

.speed-test-container {
  width: 960px;
  padding: 1em;
}

.canvas-container {
  margin-top: 50px;
}

#canvas-wrapper {
  margin: 0 auto;
  position: relative;
}

#canvas-wrapper {
  height: 250px;
  width: 250px;
  border-radius: 50%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to bottom, #e53d8b, #2391de);
}

#canvas-wrapper.download {
  background: #249ade;
  background: var(--light-blue);
  animation-name: downloadAnimation;
  animation-duration: 0.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

#canvas-wrapper.upload {
  background: #e53d8b;
  background: var(--pink);
  box-shadow: 0px 0px 15px 0px #e53d8b;
  animation-name: uploadAnimation;
  animation-duration: 0.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes downloadAnimation {
  from {
    box-shadow: 0px 0px 15px 0px #2391de;
  }
  to {
    box-shadow: 0px 0px 15px 2.5px #2391de;
  }
}

@keyframes uploadAnimation {
  from {
    box-shadow: 0px 0px 15px 0px #e53d8b;
  }
  to {
    box-shadow: 0px 0px 15px 2.5px #e53d8b;
  }
}

canvas {
  transform: rotateZ(-45deg);
  border-radius: 50%;
  background-color: #181830;
  background-color: var(--background-color-dark);
}

button {
  position: absolute;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.three-js-container canvas {
  background: grey;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
}

.header .app-title {
  font-size: 2em;
  margin: 0;
}

.header .app-icon {
  font-size: 2em;
}

@media screen and (max-width: 400px) {
  .header .app-title {
    font-size: 1.5rem;
  }
}

.wave-loader-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.wave-loader-container svg {
  margin-left: 10px;
}

.wave-loader-container path {
  stroke: #fff;
  stroke-width: 5px;
  stroke-linecap: round;
  fill: none;
}

.isp-container {
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

.isp-name {
  font-size: 2em;
  text-align: center;
}

@media screen and (max-width: 400px) {
  .isp-name {
    font-size: 1.5em;
  }
}

.go-button {
  height: calc(100% - 10px);
  width: calc(100% - 10px);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 1s ease-in;
  background-color: var(--background-color-dark);
}

.button-text {
  font-size: 1.5em;
}

.stats-container {
  display: flex;
  margin-top: 100px;
  height: 150px;
}

.dowload-speed-wrapper,
.upload-speed-wrapper {
  flex: 1 1;
  display: flex;
  justify-content: center;
  transition: all 0.5s ease-in;
}

.stat-header {
  display: flex;
  align-items: center;
}

.stat-title {
  margin-left: 5px;
  font-weight: bold;
}

.stat-icon {
  font-size: 1.5em;
}

.download-speed-container,
.upload-speed-container {
  width: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.upload-speed-container {
  transition: all 0.5s ease-in;
}

.download-speed-container .stat-icon {
  color: var(--light-blue);
}

.upload-speed-container .stat-icon {
  color: var(--pink);
}

.stat-value {
  font-size: 2em;
  margin: 10px 0 10px 10px;
}

.download-speed-container .stat-value {
  color: #fff;
  text-shadow: 0 0 0.125em var(--light-blue);
}

.upload-speed-container .stat-value {
  color: #fff;
  text-shadow: 0 0 0.125em var(--pink);
}

.stat-unit {
  font-size: 1em;
  margin-top: 5px;
  margin-left: 15px;
}

.speed-counter-container {
  position: absolute;
}
.speed-counter-container .speed,
.speed-counter-container .unit {
  font-size: 3em;
  color: #fff;
  text-shadow: 0 0 0.125em var(--light-blue);
  display: block;
  text-align: center;
}

.speed-counter-container .unit {
  font-size: 1.5rem;
}

.speed-counter-container .unit.pink,
.speed-counter-container .speed.pink {
  text-shadow: 0 0 0.125em var(--pink);
}

.ease-in {
  animation-name: slide-in;
}

@keyframes slide-in {
  from {
    opacity: 0;
    top: -100px;
  }
  to {
    opacity: 1;
    top: 0;
  }
}

